import React, { useState, useEffect } from "react";
import axios from "axios";
import "../../styles/layout/layout.css";
import "../../styles/layout/header.css";
import "../../styles/user/userBox.css";
import "../../styles/user/userIcon.css";
import "../../styles/user/userHeader.css";
import "../../styles/deleteAccount.css";

import { useForm } from "react-hook-form";
import UserProfile from "../../components/UserProfile";
import HeaderNoProfile from "../../components/Header/HeaderNoProfile";


const DeleteAccount = () => {

	type ResponseList = {
		passwordError: boolean;
	}
	const [response, setResponse] = useState<ResponseList>({
		passwordError: false
	});

	const [submitted, setSubmitted] = useState(false);
	
	const onSubmit = async (data: any) => {
		await new Promise((r) => setTimeout(r, 100));

		// alert(JSON.stringify(data));
		console.log(data);

		await axios
			.post("/mypage/delete/account", data)
			.then((res) => {
				console.log("postHere");
				console.log(data);
				setResponse(res.data);
				console.log(res.data);
				setSubmitted(true);
			})
			.catch(function (error) {
				console.log(error.config);
			});
	};

	function RedirectAndInputErrors(){
		if(response.passwordError){
			return (
			<div className="user-text-error">passwordError</div>
			)
		}
		else if(submitted) {
			//     loginForm 
			window.location.href = "/loginForm"
			return (null);
		}
		return (null);
	}

	const {
		register,
		handleSubmit,
		formState: { isSubmitting, isDirty, errors },
	// eslint-disable-next-line react-hooks/rules-of-hooks
	} = useForm();

	const [user, setUser] = useState<any>([]);

	useEffect(() => {
		axios
			.get("/mypage/info")
			.then((res) => {
				setUser(res.data);
				console.log(res.data);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);
	
	function ButtonToUserInfo(){
		function handleClick(e: any){
				window.location.href="/mypage/info"
		}
			return(
				<button className="user-btn deleteAccount-btn-cancel" onClick={handleClick}>
						<div className="user-btn-text deleteAccount-btn-text-cancel">  </div>
				</button>

			)
	}
	
	// PW toggle start.
	const [showPw, setShowPw] = useState<boolean>(false);
	const toggleShowPw =()=>{
	setShowPw(!showPw);
	}

	function handlePwClick(e: any){
    toggleShowPw()
  }

	function ShowPw(props: any) {
  return(
    <button value="" className="user-show-pw" onClick={handlePwClick}></button>
  	)
	}

	function HidePw(props: any) {
  return(
    <button value="" className="user-hide-pw" onClick={handlePwClick}></button>
  	)
	}
	// PW toggle fin.

	return (
	<form onSubmit={handleSubmit(onSubmit)}>
		<div className="container">
			<HeaderNoProfile />
      <div className="userInfo-profile-container">
				<UserProfile/>
				<div className="user-profile-name">{user.userRingcardName}</div>
			</div>

			<div>
				<div className="user-box">
					{/* width        ! */}
					<div className="user-box-in" style={{height:'120px'}}>
						<div className="user-text deleteAccount-text"> ,          .
						?  
						</div>
					</div>

					{/* <div className="user-box-in">
						<div className="user-text deleteAccount-text">   . </div>
						<input className="user" placeholder=" " {...register("password", {
							required: "  .",
							})}>
						</input>
					</div> */}

					<div className="user-box-in">
						<div className="user-text deleteAccount-text">    .</div>
						<div className="user-box-div user-icon-light">
							<input
							type={showPw ? "text" : "password"}
							className="user-inner-transparent"
							placeholder=" "
              {...register("password", {
							required: "  .",
							})}></input>
							{showPw ? (
	  					<ShowPw onClick={toggleShowPw} />
    					) : (
	  					<HidePw onClick={toggleShowPw} />
    					)}
						</div>
					</div>

					{/* <div className="user-box-in">
						<div className="user-text deleteAccount-text"> </div>
						<input className="input deleteAccount-input" placeholder=" "></input>
					</div>
					<div className="user-box-in">
						<div className="user-text deleteAccount-text">  </div>
						<input className="input deleteAccount-input" placeholder="  "></input>
					</div> */}

					<div className="user-box-in">
            <RedirectAndInputErrors/>
          </div>

					<div className="user-box-in">
						<button className="user-btn deleteAccount-btn">
						<div className="user-btn-text"></div>
						</button>
					</div>

					<div className="user-box-in">
						<ButtonToUserInfo/>
					</div>
				</div>
			</div>
			{/*   ?? */}
			{/* <div className="deleteAccount-delete-box">
				<div className="deleteAccount-delete">
					<a className="deleteAccount-delete-text" href="/">    </a>
				</div>
			</div> */}
		</div>
	</form>
	);
};
export default DeleteAccount;